import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { Post, BlogService } from '../blog.service';
import { Parser, HtmlRenderer } from 'commonmark';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.css']
})

export class PreviewComponent implements OnInit {
  post: Post;
  title: String;
  body: String;
  private parser: Parser = new Parser();
  private htmlRenderer: HtmlRenderer = new HtmlRenderer();
  
  constructor(
    private blogService: BlogService,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => this.onPreview(params));
  }

  onPreview(params: Params) {
    let id = parseInt(params['id']);

    this.post = this.blogService.getPost(id);
    if (this.post) {
      this.title = this.htmlRenderer.render(this.parser.parse(this.post.title));
      this.body = this.htmlRenderer.render(this.parser.parse(this.post.body));
    } else {
      this.router.navigateByUrl('/');
    }
  }
}


